.hidden {
	display: none; }

.share {
	@include flexbox;
	align-items: center;
	align-content: center;
	> div {
		+ div {
			margin-left: 10px; } }
	.google {
		iframe {
			margin-top: -2px; } }
	.tweet {
		iframe {
			margin-top: 2px; } } }

// **********SEARCH-PAGE**********
.searchresults {
	.pagination {
		display: none; }
	.altcontent1 {
		display: none; }
	.pagination {
		text-align: left; }
	.banner-home {
		display: none; }
	.product-page {
		width: 100%;
		.product-list {
			.col-xs-12 {
				padding: 0;
				width: 20%;
				@include media-breakpoint-down(lg) {
					width: 20%; }
				@include media-breakpoint-down(md) {
					width: 25%; }
				@include media-breakpoint-down(sm) {
					width: 33.33%; }
				@include media-breakpoint-down(xs) {
					width: 50%; } } } } }
