// SEARCH SUGGEST
#map {
	iframe {
		pointer-events: none;
		&.active {
			pointer-events: all; } } }

// form
input[type="reset"] {
	display: none; }

// Pagination
.product-detail-page, .news-detail-page, .project-detail-page {
	.pages {
		display: none; } }

.pages {
	ul.pagination {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		li {
			display: block;
			+ li {
				margin-left: 5px; }
			a,span {
				@include flexbox;
				width: 36px;
				height: 36px;
				justify-content: center;
				align-items: center;
				align-content: center;
				color: $main;
				background: $white;
				@include fs(14rem);
				@include bor-rad(5px);
				@include media-breakpoint-down(sm) {
					@include fs(12rem);
					width: 30px;
					height: 30px; } }
			&:hover {
				a,span {
					color: $blue; } }
			&.active {
				a,span {
					background: $main;
					color: $white; } } } } }
