// FORM INIT
.form-wrap {
	// .module-title
	// 	font-weight: 500
	// 	+fs(14rem)
	// 	color: $blue
	// 	text-transform: uppercase
	// 	margin-bottom: 5px
	.form-group {
		margin-bottom: 10px; }
	label {
		display: block;
		@include fs(13rem);
		color: darken($white,60%);
		// font-weight: 500
		padding: 8px 0; }
	input, select, textarea {
		@include fs(14rem);
		padding: 10px 20px;
		@include bor-rad(5px);
		border: 1px solid darken($white,10%);
		width: 100%;
		color: $main;
		@include bor-rad(0);
		@include transition(all,0.5s);
		&::-webkit-input-placeholder {
			color: inherit;
			opacity: 0.5; }
		&:active, &:focus {
			border-color: lighten($color-default,10%);
			box-shadow: 0 0 0 0.2rem color-rgba($color-default,0.2); } }
	select {
		padding: 9px 15px; }
	.gender {
		@include flexbox;
		flex-wrap: wrap;
		padding: 9px 0; }
	.checkbox {
		label {
			width: 100%; } }
	input[type="submit"] {
		@extend .btn-main;
		width: 100%;
		border: none;
		@include bor-rad(0); } }
.form-des {
	@include fs(13rem);
	color: darken($white,40%);
	font-style: italic; }
.form-link {
	@include fs(14rem);
	color: darken($white,40%);
	+ .form-link {
		margin-left: 10px; }
	&:hover {
		color: $blue;
		border: none; } }
.form-hidden {
	display: none;
	padding: 10px 0; }

// register cms
html {
	body {
		.form-wrap {
			.RadInput_Default .riTextBox, .RadInputMgr_Default {
				@include fs(13rem);
				padding: 7px 15px;
				@include bor-rad(5px);
				border: 1px solid darken($white,10%);
				width: 100%;
				color: $main;
				height: auto !important;
				@include transition(all,0.5s);
				&::-webkit-input-placeholder {
					color: inherit;
					opacity: 0.5; }
				&:active, &:focus {
					border-color: lighten($blue,10%);
					box-shadow: 0 0 0 0.2rem color-rgba($blue,0.2); } } } } }



// FORM COMPONENTS
.input-number {
	@include flexbox;
	border: 1px solid darken($white,5%);
	@include bor-rad(0px);
	background: $white;
	width: 72px;
	.btn-spin {
		width: 20px;
		height: 22px;
		@include fs(14rem);
		line-height: 22px;
		text-align: center;
		font-weight: 700;
		cursor: pointer; }
	input {
		width: 30px;
		margin: 0;
		padding: 0;
		border: none;
		line-height: 22px;
		text-align: center;
		@include fs(13rem);
		font-weight: 700; } }

.checkbox {
	input {
		display: none;
		+ label {
			color: $main;
			padding: 0 0 0 20px;
			@include pos(rel,null);
			@include fs(13rem);
			&:hover {
				color: $blue; }
			&:before {
				content: '';
				width: 14px;
				height: 14px;
				@include bor-rad(3px);
				border: 1px solid darken($white,20%);
				@include pos(abs, 2px null null 0); }
			&:after {
				content: '\f00c';
				width: 14px;
				height: 14px;
				@include bor-rad(3px);
				color: $blue;
				line-height: 1;
				@include pos(abs, 2px null null 1px);
				z-index: 1;
				opacity: 0; } }
		&:checked {
			+ label {
				&:after {
					opacity: 1; } } } } }
.radio {
	input {
		display: none;
		+ label {
			color: $main;
			padding: 0 0 0 20px;
			@include pos(rel,null);
			@include fs(14rem);
			&:hover {
				color: $blue; }
			&:before {
				content: '';
				width: 14px;
				height: 14px;
				@include bor-rad(50px);
				border: 1px solid darken($white,20%);
				@include pos(abs, 2px null null 0); }
			&:after {
				content: '';
				width: 8px;
				height: 8px;
				@include bor-rad(50px);
				background: $blue;
				@include pos(abs, 5px null null 3px);
				z-index: 1;
				opacity: 0; } }
		&:checked {
			+ label {
				&:after {
					opacity: 1; } } } } }
.file {
	input[type="file"] {
		display: none; }
	label {
		&:hover {
			color: $black; } } }
// SOCIAL LOGIN
.social-login {
	ul {
		li {
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 0; }
			a {
				@extend .btn-outline;
				@include flexbox;
				align-items: center;
				align-content: center;
				justify-content: center;
				padding: 0;
				background: none;
				padding: 7px 0;
				text-transform: capitalize;
				font-weight: 500;
				.fa {
					@include fs(18rem); }
				span {
					display: block;
					line-height: 1;
					padding: 0px 10px;
					text-align: center; }
				&.facebook {
					border-color: $facebook;
					color: $facebook;
					&:hover {
						background: $facebook;
						color: $white; } }
				&.google {
					border-color: $google;
					color: $google;
					&:hover {
						background: $google;
						color: $white; } } } } } }
