.breadcrumb-wrap {
	@include pos(rel,null);
	.breadcrumb-bg {
		height: 300px;
		filter: brightness(70%); }
	.breadcrumb {
		@include pos(abs,50px null null null);
		z-index: 1;
		margin: 0;
		padding: 5px 0;
		@include bor-rad(0);
		background: none;
		border-bottom: 1px solid $white;
		@include fs(14rem);
		color: $white;
		li {
			+ li {
				&:before {
					content: '\e876';
					font-family: Linearicons-Free;
					@include fs(10rem);
					padding: 0px 6px; } }
			a {
				color: $white; } } }
	.page-name {
		@include pos(abs,100px null null null);
		@include fs(48rem);
		font-weight: 700;
		color: $white; } }

.breadcrumb-wrap2 {
	background: $white;
	@include pos(rel,null);
	.breadcrumb-flex {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: space-between;
		padding: 8px 0; }
	.page-name {
		@include fs(18rem);
		font-weight: 500; }
	.breadcrumb {
		z-index: 1;
		margin: 0;
		padding: 5px 0;
		@include bor-rad(0);
		background: none;
		border-bottom: 1px solid $white;
		@include fs(14rem);
		color: darken($white,40%);
		li {
			+ li {
				&:before {
					content: '\e876';
					font-family: Linearicons-Free;
					@include fs(10rem);
					padding: 0px 8px; } }
			&:last-child {
				a {
					color: $main; } } } } }
