.sidebar-categories {
	margin-top: 40px;
	background: darken($white,5%);
	.categories {
		ul {
			li {
				margin-bottom: 5px;
				a {
					display: block;
					padding: 8px 20px;
					@include fs(13rem);
					text-transform: capitalize;
					background: $white; }
				&:hover {
					a {
						color: $color-default; } }
				&.active {
					a {
						color: $white;
						background: $color-primary; } } } } } }

.picture-page {
	margin: 40px 0;

	.album-list {
		.album-item {
			background: $white;
			margin-bottom: 30px;
			cursor: pointer;
			.img {
				img {
					width: 100%;
					height: 200px;
					object-fit: cover; } }
			.info {
				padding: 10px 20px;
				.name {
					@include fs(15rem);
					font-weight: 500; } } } }

	.card-columns {
		.card {
			margin-bottom: 20px;
			border: none;
			box-shadow: none; } } }

.video-page {
	margin: 40px 0;
	.video-list {
		.video-item {
			@include pos(rel,null);
			display: block;
			margin-bottom: 30px;
			@include transition(all,0.3s);
			&:hover {
				box-shadow: 10px 10px 20px 0px rgba(0,0,0,0.04), -10px 0 20px 0px rgba(0,0,0,0.04);
				.img {
					img {
						@include transform(scale3d(1.1,1.1,1)); } } }
			.img {
				overflow: hidden;
				img {
					width: 100%;
					height: 230px;
					object-fit: cover;
					@include transition(all,0.3s); } }
			.info {
				@include pos(abs, null 0 0 0);
				z-index: 3;
				padding: 15px 20px;
				padding-left: 80px;
				background: color-rgba($black,0.5);
				color: $white;
				&:before {
					content: '\f144';
					@include fs(48rem);
					@include pos(abs, 50% null null 20px);
					@include transform(translateY(-50%)); }
				.name {
					@include fs(18rem);
					line-height: 1.2;
					font-weight: 700;
					@include textover(2); }
				time {
					@include fs(12rem);
					line-height: 1;
					opacity: 0.7; } } } }
	.video-block {
		background: $white;
		@include flexbox;
		.video-frame {
			width: 70%; }
		.video-list {
			width: 30%;
			height: 440px;
			overflow: auto;
			&::-webkit-scrollbar-track {
				background-color: darken($white, 20%);
				@include bor-rad(100px); }
			&::-webkit-scrollbar {
				width: 6px;
				background-color: #fff;
				@include bor-rad(100px); }
			&::-webkit-scrollbar-thumb {
				background-color: $color-primary;
				@include bor-rad(100px); }
			.video-item {
				@include flexbox;
				padding: 10px 15px;
				@include pos(rel,null);
				&:before {
					content: '';
					display: block;
					@include pos(abs, 0 null 0 0);
					width: 5px;
					background: $color-default;
					opacity: 0; }
				&.active {
					background: color-rgba($color-default, 0.05);
					&:before {
						opacity: 1; }
					.info {
						.name {
							color: $color-default; } } }
				.img {
					width: 120px; }
				.info {
					flex: 1;
					padding: 5px 10px;
					.name {
						@include fs(14rem);
						line-height: 1.3;
						font-weight: 500; }
					time {
						@include fs(11rem);
						opacity: 0.5; } } } } } }
