.compare-panel {
	@include pos(fix, null 0 0 0);
	z-index: 300;
	@include transition(all,0.3s);
	background: $white;
	border-top: 1px solid darken($white,10%);
	// display: none
	box-shadow: 0 -10px 20px -10px color-rgba($black,0.1);
	@include transform(translateY(140%));
	@include transition(all,0.3s);
	&.active {
		@include transform(translateY(0)); }
	&.hide {
		@include transform(translateY(100%)); }
	.container {
		@include media-breakpoint-down(sm) {
			padding: 0; } }
	.compare-toggle {
		@include pos(abs, null 0 100% null);
		background: darken($white,10%);
		@include bor-rad(5px 0px 0 0);
		@include flexbox;
		@include fs(13rem);
		padding: 10px;
		border-top: 1px solid darken($white,10%);
		border-left: 1px solid darken($white,10%);
		.btn-minimize {
			color: $blue;
			padding: 0px 10px;
			cursor: pointer;
			@include transition(all,0.3s);
			&.active {
				.material-icons {
					@include transform(scaleY(-1)); } }
			&:hover {
				color: $blue; } }
		.btn-closecompare {
			border-left: 1px solid darken($white,(10%));
			padding-left: 10px;
			color: $red;
			cursor: pointer;
			@include transition(all,0.3s);
			&:hover {
				color: $red; } }
		.material-icons {
			@include fs(16rem); } }

	.compare-list {
		@include flexbox;
		flex-wrap: wrap;
		@include media-breakpoint-down(sm) {
			// flex-wrap: nowrap
			// border-left: 1px solid darken($white,10%)
			// border-right: 1px solid darken($white,10%)
			overflow: auto; }
		.product {
			padding: 15px;
			@include pos(rel,null);
			@include media-breakpoint-down(sm) {
				width: 33.33%;
				// + .product
				// 	border-left: 1px solid darken($white,10%)
				border-left: 1px solid darken($white,10%);
				&:nth-child(3) {
					border-right: 1px solid darken($white,10%); } }
			@include media-breakpoint-up(md) {
				width: 33.33%; }
			.remove-cpr {
				@include pos(abs,10px 10px null null);
				cursor: pointer;
				display: block;
				width: 20px;
				height: 20px;
				line-height: 18px;
				text-align: center;
				@include bor-rad(50px);
				&:hover {
					color: $red;
					background: lighten($red,30%); } }
			figure {
				@include flexbox;
				flex-wrap: wrap;
				.product-img {
					width: 80px;
					height: 80px;
					@include flexbox;
					align-items: center;
					align-content: center;
					justify-content: center;
					picture {
						width: 100%;
						height: 100%;
						@include flexbox;
						justify-content: center;
						align-items: center; }
					img {
						max-width: 90%;
						max-height: 90%; } }
				figcaption {
					width: calc(100% - 80px);
					padding: 10px;
					@include media-breakpoint-down(sm) {
						display: none; }
					.product-name {
						@include fs(14rem);
						@include media-breakpoint-down(lg) {
							@include fs(12rem); }
						@include media-breakpoint-down(sm) {
							margin-top: 10px; }
						a {
							color: $black; } }
					.product-price {
						.new {
							color: $red;
							font-weight: 600;
							@include fs(13rem); }
						.old {
							@include fs(11rem);
							color: darken($white,40%);
							text-decoration: line-through; } } } }
			.search {
				@include flexbox;
				justify-content: center;
				align-items: center;
				align-content: center;
				width: 100%;
				height: 100%;
				.searchbox {
					@include bor-rad(5px);
					border: 1px solid darken($white,10%);
					@include flexbox;
					align-items: center;
					align-content: center;
					width: 100%;
					input {
						width: calc(100% - 40px);
						background: none;
						border: none;
						line-height: 32px;
						padding: 0px 10px;
						@include fs(14rem); }
					.searchbutton {
						width: 40px;
						@include flexbox;
						align-items: center;
						align-content: center;
						justify-content: center; } } } } }
	.btn-wrap {
		height: 100%;
		border-left: 1px solid darken($white,10%);
		border-right: 1px solid darken($white,10%);
		@include flexbox;
		flex-wrap: wrap;
		justify-content: center;
		align-content: center;
		align-items: center;
		padding: 0px 15px;
		.btn-compare, .btn_compare_b {
			display: block;
			width: 100%;
			line-height: 36px;
			@include fs(13rem);
			margin-bottom: 10px;
			background: $blue;
			padding: 0px 20px;
			@include bor-rad(50px);
			text-align: center;
			color: $white;
			text-transform: uppercase;
			font-weight: 500;
			&:hover {
				background: $main; } }
		.des {
			color: darken($white,60%);
			@include fs(12rem); } } }

.compare-page {
	padding: 40px 0;
	.page-title {
		@include fs(18rem);
		margin-bottom: 15px; }
	.compare-product-table {
		width: 100%;
		border: 1px solid darken($white,10%);
		th, td {
			border-bottom: 1px solid darken($white,10%);
			padding: 10px;
			background: none;
			@include fs(13rem);
			text-align: left;
			vertical-align: middle;
			&:first-child {
				font-weight: 500;
				@include media-breakpoint-down(md) {
					width: 100%; } }
			&:nth-child(2) {
				@include media-breakpoint-down(md) {
					border-left: none; } }
			strong {
				font-weight: 500;
				a {
					display: block;
					color: $blue; }
				.fa {
					font-weight: normal; } }
			.fa-check-circle {
				@include fs(18rem);
				color: $green; }
			+ th, + td {
				border-left: 1px solid darken($white,10%);
				width: 40%;
				@include media-breakpoint-down(md) {
					width: 50%; } } }
		tr {
			@include media-breakpoint-down(md) {
				@include flexbox;
				flex-wrap: wrap; }
			&:last-child {
				th, td {
					border-bottom: none; } }
			&.title {
				text-transform: uppercase;
				a {
					font-weight: 500;
					color: $blue;
					&:hover {
						color: $red; } } }
			&.specs-match {
				background: lighten($blue,50%); } }
		.toggle-diff {
			// +mainbutton($lblue,$blue)
			display: block;
			line-height: 40px;
			@include fs(14rem);
			font-weight: normal; }
		.product-header {
			@include media-breakpoint-down(md) {
				th, td {
					+ th, + td {
						padding: 0; } } }
			.product-col {
				figure {
					.product-img {
						@include flexbox;
						justify-content: center;
						align-items: center;
						height: 200px;
						img {
							max-height: 100%;
							max-width: 100%;
							width: auto; } } } } } } }

// COMPARE DETAIL

.compare-products {
	padding: 40px 15px;
	@include media-breakpoint-down(lg) {
		padding: 15px; }
	.body-wrap {
		.clear-list {
			display: block;
			margin: -10px 0 20px;
			color: darken($white,60%);
			@include fs(14rem);
			&:before {
				content: '\f057';
				margin-right: 5px;
				color: lighten($red,20%); } } }
	.compare-products-table {
		border: 1px solid darken($white,10%);
		@include bor-rad(5px);
		overflow: hidden;
		.btn-primary {
			margin: 0;
			padding: 0;
			background: none;
			border: none;
			// +mainbutton($lblue,$blue)
			line-height: 36px;
			@include fs(14rem);
			// text-transform: uppercase
			display: inline-block;
			padding: 0px 20px;
 }			// margin-top: 40px
		tr {
			&.specs-match {
				background: lighten($blue,50%); }
			&.specs-group {
				td {
					background: darken($white, 5%)!important; }
				a {
					@include fs(17rem);
					font-weight: 600;
					line-height: 1.4;
					color: $main;
					.fa {
						@include fs(15rem);
						color: $main; } } } }
		th, td {
			border-left: 1px solid darken($white,10%);
			border-bottom: 1px solid darken($white,10%);
			@include pos(rel,null);
			@include fs(14rem);
			padding: 10px;
			color: darken($white,60%);
			.fa {
				color: darken($lightgreen, 10%); }
			@media (max-width: 414px) {
				max-width: 128px;
				min-width: 128px;
				&.odd {
					max-width: 191px;
					min-width: 191px; } }
			@media (max-width: 380px) {
				&.odd {
					max-width: 171px;
					min-width: 171px; } }
			@media (max-width: 375px) {
				max-width: 115px;
				min-width: 115px; }
			&:first-child {
				border-left: none;
				font-weight: 600;
				color: $black; } }
		.product-header {
			background: $white;
			td {
				background: $white;
				padding: 0;
				text-align: center;
				@include media-breakpoint-up(lg) {
					&:first-child {
						vertical-align: middle; } } }
			&.scroll-to-fixed-fixed {
				td {
					@include media-breakpoint-down(xs) {
						display: block;
						float: left; }
					@media (max-width: 414px) {
						width: 128px!important;
						&.odd {
							width: 191px!important; } }
					@media (max-width: 375px) {
						width: 115px!important; }
					@include media-breakpoint-up(xl) {
						min-width: 298px; } }
				td:nth-child(1) {
					@include media-breakpoint-up(xl) {
						min-width: 301px; } }
				.product-item {
					.title {
						@include media-breakpoint-down(xs) {
							display: none; } } } }
			.product-item {
				@include pos(rel,null);
				padding: 20px 0;
				@include media-breakpoint-up(md) {
					@include flexbox;
					align-items: flex-start;
					align-content: flex-start; }
				.remove-button {
					@include pos(abs,10px 10px null null);
					@include fs(12rem);
					color: darken($white,40%);
					background: none;
					border: none;
					&:hover {
						color: $red; } }
				.image {
					width: 80px;
					@include media-breakpoint-down(sm) {
						display: block;
						margin: 0 auto;
						width: 50px; }
					.product-image {
						display: block;
						width: 50px;
						height: 50px;
						@include media-breakpoint-up(md) {
							@include flexbox;
							width: 80px;
							height: 80px;
							justify-content: center;
							align-items: center; }
						img {
							max-width: 90%;
							max-height: 90%;
							@include media-breakpoint-down(sm) {
								max-height: 45px;
								min-height: 45px;
								object-fit: cover; } } } }
				.title {
					text-align: left;
					padding: 10px;
					// +media-breakpoint-down(sm)
					// 	display: none
					@include media-breakpoint-up(md) {
						width: calc(100% - 80px); }
					.product-name {
						@include media-breakpoint-down(sm) {
							//min-height: 3rem
							white-space: nowrap;
							overflow: hidden;
							text-overflow: ellipsis; }
						a {
							color: $black;
							@include fs(14rem);
							@include media-breakpoint-down(lg) {
								@include fs(12rem); } } }
					.old-price {
						color: $red;
						font-weight: 600;
						@include fs(12rem); }
					.new-price {
						color: $red;
						font-weight: 600;
						@include fs(12rem); } } } }
		.title {
			a {
				@include fs(17rem);
				font-weight: 600;
				color: $main;
				line-height: 1.4;
				.fa {
					@include fs(15rem);
					color: $main; } } }
		.fa-check-circle {
			color: $green;
			@include fs(18rem); } }
	.back-link {
		.toggle-diff {
			display: inline-block;
			height: 36px;
			line-height: 36px;
			background: $bluegradient;
			@include fs(14rem);
			border: none;
			width: 230px;
			text-align: center;
			font-weight: normal;
			padding: 0 10px;
			@include media-breakpoint-down(md) {
				@include pos(fixed, null 0 10px 0);
				z-index: 1;
				margin: 0 auto; }
			&:hover {
				box-shadow: 0px 10px 20px -10px $blue; } }
		@include media-breakpoint-down(md) {
			&:after {
				content: '';
				display: block;
				background: $white;
				@include pos(fixed, null 0 0 0);
				height: 62px;
				z-index: 0; } }
		.clear-list {
			display: inline-block;
			margin: 30px 0;
			// +mainbutton($lpurple, $dpurple)
			@include fs(14rem);
			line-height: 36px;
			padding: 0px 30px;
			@include bor-rad(50px);
			&:before {
				content: '\f104';
				color: $white;
				margin-right: 10px; } } } }
.compare-products {
	&.container {
		@include pos(rel,null);
		background: $white;
		@include bor-rad(5px);
		overflow: hidden;
		box-shadow: 0px 5px 20px color-rgba($black,0.05);
		padding: 15px;
		margin: 20px 0;
		@include media-breakpoint-down(md) {
			width: calc(100% + 30px);
			max-width: calc(100% + 30px);
			margin: 0 -15px;
			padding: 15px;
			@include bor-rad(0);
			box-shadow: none; } }
	.page-title {
		margin-bottom: 15px; }
	.body-wrap {
		.clear-list {
			margin-top: 0; } }
	.back-link {
		@include media-breakpoint-down(md) {
			&:after {
				display: none !important; } }
		.clear-list {
			@include mainbutton;
			background: $purplegradient;
			color: $white;
			font-weight: normal;
			@include fs(13rem);
			@include bor-rad(50px);
			&:hover {
				box-shadow: 0px 10px 20px -10px $purple; } } } }
