.product-item {
	height: 100%;
	@include pos(rel,null);
	@include media-breakpoint-down(lg) {
		height: 100%; }
	.btn_compare {
		// display: none
		@include pos(abs,0 null 0 0);
		right: 0;
		@include fs(0);
		visibility: hidden;
		opacity: 0;
		z-index: 10;
		&:before {
			content: '\f00c';
			display: block;
			width: 30px;
			height: 30px;
			line-height: 30px;
			text-align: center;
			background: darken($white,5%);
			@include bor-rad(50px);
			@include fs(16rem);
			margin-top: 10px;
			margin-left: 10px;
			color: darken($white,40%); }
		&.active {
			&:before {
				// border-color: color-rgba($white,0)
				background: $blue;
				color: $white; } } } }

.product-col {
	display: block;
	height: 100%;
	padding: 10px 15px;
	background: $white;
	// border: 1px solid darken($white,5%)
	@include bor-rad(5px);
	@include transition(all,0.5s);
	@include media-breakpoint-down(md) {
		padding: 10px; }
	figure {
		@include pos(rel,null);
		.product-img {
			@include flexbox;
			height: 200px;
			justify-content: center;
			align-items: center;
			align-content: center;
			overflow: hidden;
			// border-bottom: 1px solid darken($white,5%)
			@include media-breakpoint-down(lg) {
				margin: 0px -5px;
				height: 180px; }
			@include media-breakpoint-down(sm) {
				height: 160px;
				margin: 0px -10px; }
			picture {
				width: 100%;
				height: 100%;
				@include flexbox;
				justify-content: center;
				align-items: center;
				align-content: center; }
			img {
				// max-width: 100%
				// max-height: 100%
				// +media-breakpoint-down(sm)
				max-width: 90%;
				max-height: 90%;
				@include transition(all,0.5s); }
			a {
				display: block;
				text-align: center; } }
		figcaption {
			padding-top: 10px;
			.product-name {
				@include fs(14rem);
				line-height: 1.3;
				font-weight: 500;
				color: $blue;
				height: 35px;
				@include textover(2);
				@include transition(all,0.5s);
				@include media-breakpoint-down(md) {
					@include fs(13rem);
					height: 32px; }
				a {
					color: $blue; } }
			.product-price {
				margin-top: 5px;
				@include pos(rel,null);
				.promotion {
					@include pos(abs, -2px 0 null null);
					@include fs(11rem);
					font-weight: 500;
					color: $white;
					padding: 0px 5px;
					@include bor-rad(3px);
					&.off {
						background: lighten($red,10%); } }
				.new {
					@include fs(14rem);
					line-height: 1;
					font-weight: 700;
					color: $black;
					@include media-breakpoint-down(md) {
						@include fs(13rem); } }
				.old {
					font-weight: 500;
					color: $bluegrey;
					@include fs(13rem);
					line-height: 1;
					color: darken($white,40%);
					text-decoration: line-through;
					@include media-breakpoint-down(md) {
						@include fs(12rem); } } }
			.product-rating {
				@include flexbox;
				align-items: center;
				align-content: center;
				padding: 5px 0;
				flex-wrap: wrap;
				@include media-breakpoint-down(lg);
				.br-wrapper {
					margin-right: 5px; }
				span {
					@include fs(12rem);
					color: darken($white,40%); } }
			.product-des {
				@include fs(12rem);
				line-height: 1.4;
				white-space: normal;
				margin-top: 5px;
				color: darken($white,60%);
				ul {
					list-style-type: disc;
					padding-left: 20px;
					li {} } } } }
	&:hover {
		@include media-breakpoint-up(xl) {
			box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.1);
			figure {
				.product-img {
					img {
						@include transform(scale3d(1.1,1.1,1)); } }
				figcaption {
					.product-name {
						color: $blue; } } } } } }

