.product-sort {
	@include flexbox;
	flex-wrap: wrap;
	background: $white;
	padding: 10px 15px;
	&:after {
		width: 100%; }
	@include media-breakpoint-up(md) {
		justify-content: space-between;
		align-items: center;
		align-content: center; }
	@include media-breakpoint-up(lg) {
		background: none;
		+ .productlist {
 } }			// margin-top: 15px
	.click-sort {
		@include flexbox;
		height: 32px;
		align-items: center;
		align-content: center;
		@include media-breakpoint-down(sm) {
			display: none; }
		a {
			display: block;
			padding-left: 20px;
			@include pos(rel,null);
			@include fs(13rem);
			color: darken($white,60%);
			+ a {
				margin-left: 20px; }
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				@include bor-rad(50px);
				border: 1px solid darken($white,20%);
				@include pos(abs, 2px null null 0); }
			&:after {
				content: '';
				display: block;
				width: 8px;
				height: 8px;
				@include bor-rad(50px);
				background: $blue;
				@include pos(abs, 5px null null 3px);
				opacity: 0; }
			&:hover {
				color: $black; }
			&.active {
				color: $blue;
				&:after {
					opacity: 1; } } } }
	.sort-type {
		width: calc(100% - 105px);
		line-height: 30px;
		@include flexbox;
		align-items: center;
		align-content: center;
		@include bor-rad(5px);
		padding: 0px 10px;
		color: darken($white,40%);
		border: 1px solid darken($white,5%);
		@include pos(rel,null);
		@include media-breakpoint-up(md) {
			display: none; }
		&:after {
			content: '\f107';
			@include pos(abs, 7px 10px null null);
			line-height: 1; }
		label {
			@include fs(14rem);
			font-weight: 300;
			margin-right: 10px;
			.material-icons {
				display: block; } }
		select {
			width: calc(100% - 34px);
			min-width: unset;
			border: none;
			background: none;
			appearance: none;
			-ms-appearance: none;
			-moz-appearance: none;
			-webkit-appearance: none;
			@include fs(14rem);
			color: darken($white,40%);
			// &:after
			// 	content: ''
			// 	+pos(abs, 0 null 0 0)
			// 	right: 0
 } }			// font-weight: 500
	.btn-compare {
		@include flexbox;
		align-items: center;
		align-content: center;
		line-height: 32px;
		@include bor-rad(50px);
		color: $white;
		background: $main;
		padding: 0px 10px;
		margin-left: 10px;
		@include fs(14rem);
		cursor: pointer;
		@include transition(all,0.5s);
		@include transition(font-size 0s);
		&.fly-compare {
			@include media-breakpoint-up(xl) {
				@include pos(fix, null null 10px 50%);
				@include transform(translateX(-50%));
				z-index: 50;
				margin-left: 0; } }
		&:hover {
			background: $blue;
			color: $white; }
		.material-icons {}
		&.active {
			background: $blue;
			color: $white; } } }
.btn-filterwrap {
	@include flexbox;
	@include pos(fix , null 0 0 0);
	z-index: 100;
	background: $white;
	border-top: 1px solid darken($white,5%);
	text-align: center;
	.btn-showcate {
		width: calc((100% - 40px) / 2);
		height: 40px;
		line-height: 40px;
		padding: 0px 10px;
		color: darken($white,50%);
		@include fs(14rem);
		border-left: 1px solid darken($white,5%);
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		.material-icons {
			margin-right: 5px;
 } }			// +fs(18rem)
	.btn-showfilter {
		width: calc((100% - 40px) / 2);
		height: 40px;
		line-height: 40px;
		padding: 0px 10px;
		color: darken($white,50%);
		@include fs(14rem);
		border-left: 1px solid darken($white,5%);
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		.material-icons {
			margin-right: 5px;
 } }			// +fs(18rem)
	.btn-changeview {
		width: 40px;
		height: 40px;
		line-height: 40px;
		@include fs(18rem);
		text-align: center;
		// border-left: 1px solid darken($white,10%)
		color: darken($white,50%);
		@include transition(all,0.3s);
		&:before {
			content: '\f00b';
			@include media-breakpoint-down(sm) {
				content: '\f00a'; } }
		&.active {
			&:before {
				content: '\f00a';
				@include media-breakpoint-down(sm) {
					content: '\f00b'; } } } } }

.filter-wrap {
	@include pos(fix, 0 null 0 0);
	right: 0;
	background: $white;
	z-index: 1500;
	@include transform(translateX(100%));
	@include transition(all,0.3s);
	@include media-breakpoint-up(lg) {
		@include pos(rel,unset null unset unset);
		right: unset;
		z-index: 500;
		@include transform(translateX(0));
		@include bor-rad(5px 5px 0 0);
		padding: 0px 15px;
		margin-bottom: 15px; }
	&.open {
		@include transform(translateX(0)); }
	&.category-group {
		@include media-breakpoint-up(lg) {
			display: none; } }
	&.option-group {
		// border: 1px solid darken($white,5%)
		background: $white;
		@include media-breakpoint-down(md) {
			background: $white;
			box-shadow: none;
			margin: 0; } }
	&.filter-bottom {
		margin: 0px 0px 15px;
		@include media-breakpoint-down(md) {
			display: none; } }
	.filter-header {
		@include media-breakpoint-up(lg) {
			display: none; }
		.btn-closefilter {
			color: $black;
			margin-right: 15px;
			height: 50px;
			@include flexbox;
			align-items: center;
			align-content: center;
			justify-content: center;
			float: left; }
		.filter-title {
			@include flexbox;
			float: left;
			height: 50px;
			justify-content: center;
			align-items: center;
			align-content: center;
			font-weight: 700; } }
	.filter-category {
		overflow: hidden;
		@include media-breakpoint-up(lg);
		> ul {
			> li {
				@include pos(rel,null);
				> a {
					display: block;
					padding: 10px 0;
					@include fs(13rem);
					text-transform: uppercase;
					color: $black;
					font-weight: 500;
					// border-bottom: 1px solid darken($white,10%)
					@include pos(rel,null);
					&:hover {
						color: $blue;
						&:after {
							@include transform(rotateZ(90deg)); } } }
				.btn-showsubfilter {
					.fa {
						@include fs(18rem);
						line-height: 1;
						margin-left: 10px;
						display: inline-block;
						@include transition(all,0.3s);
						@include pos(abs, 10px 10px null null); }
					&.active {
						.fa {
							@include transform(rotateZ(90deg)); } } }
				.subfilter {
					display: none;
					max-height: 300px;
					overflow: auto;
					@include bor-rad(5px);
					padding: 0 10px;
					border: 1px solid darken($white,5%);
					// +disablescroll
					ul {
						li {
							a {
								display: block;
								@include pos(rel,null);
								color: darken($white,40%);
								@include fs(12rem);
								font-weight: 500;
								padding: 10px 0;
								padding-left: 20px;
								span {
									color: darken($white,40%);
									margin-left: 5px; }
								&:before {
									content: '';
									display: block;
									width: 12px;
									height: 12px;
									@include bor-rad(50px);
									border: 1px solid darken($white,40%);
									@include pos(abs, 12px null null 0);
									z-index: 1;
									@include transition(all,0.3s); }
								&:after {
									content: '';
									display: block;
									width: 8px;
									height: 8px;
									@include bor-rad(50px);
									background: $blue;
									@include pos(abs, 14px null null 2px);
									z-index: 2;
									opacity: 0;
									@include transition(all,0.3s); } }
							&.active {
								a {
									color: $black;
									&:before {
										border-color: $blue; }
									&:after {
										opacity: 1; } } } } } }

				&.active {
					> a {
						color: $blue; }
					.btn-showsubfilter {
						.fa {
							@include transform(rotateZ(90deg)); }
						&.active {
							.fa {
								@include transform(rotateZ(0deg)); } } }
					.subfilter {
						display: block; } } } } }
	.filter-content {
		max-height: calc(100vh - 160px);
		overflow: auto;
		padding: 0 15px;
		@include media-breakpoint-up(lg) {
			height: unset;
			overflow: unset;
			padding: 0;
			@include flexbox;
			flex-wrap: wrap;
			margin: 0px -15px; }
		.filter-group {
			// +bor-rad(5px)
			// margin-bottom: 5px
			// box-shadow: 0px 5px 10px -5px color-rgba($black,0.05)
			@include media-breakpoint-up(lg) {
				padding: 0px 15px;
				@include pos(rel,null); }
			.filter-name {
				padding: 10px 0;
				@include fs(13rem);
				text-transform: uppercase;
				color: $black;
				font-weight: 500;
				// border-bottom: 1px solid darken($white,10%)
				@include pos(rel,null);
				@include media-breakpoint-up(lg) {
					padding-right: 15px;
					@include fs(13rem);
					font-weight: normal;
					text-transform: none;
					cursor: pointer;
					color: darken($white,60%); }
				&.active {
					color: $blue;
					&:after {
						@include transform(rotateZ(90deg));
						@include media-breakpoint-up(lg) {
							@include transform(scaleY(-1)); } } }
				&:after {
					content: '\f105';
					@include fs(18rem);
					line-height: 1;
					margin-left: 10px;
					display: inline-block;
					@include transition(all,0.3s);
					@include pos(abs, 10px 0 null null);
					@include media-breakpoint-up(lg) {
						content: '\f107'; } }
				.filter-tags {
					margin-top: 0;
					@include pos(abs,8px 15px null null);
					@include media-breakpoint-up(lg) {
						display: none; } } }
			.filter-option {
				display: none;
				max-height: 300px;
				overflow: auto;
				@include bor-rad(5px);
				border: 1px solid darken($white,5%);
				// +disablescroll
				@include media-breakpoint-up(lg) {
					@include pos(abs, 100% null null 0);
					z-index: 10;
					min-width: 250px;
					max-height: 250px;
					// display: block
					background: $white;
					border: 1px solid darken($white,10%);
					box-shadow: 0 10px 20px -5px color-rgba($black,0.3); }
				.filter-brand, .filter-properties, .filter-price {
					// border-top: none
					padding: 0 10px;
					a {
						display: block;
						@include pos(rel,null);
						color: darken($white,40%);
						@include fs(12rem);
						font-weight: 500;
						padding: 10px 0;
						padding-left: 20px;
						span {
							color: darken($white,40%);
							margin-left: 5px; }
						&:before {
							content: '';
							width: 14px;
							height: 14px;
							@include bor-rad(50px);
							border: 1px solid darken($white,20%);
							@include pos(abs, 11px null null 0); }
						&:after {
							content: '';
							width: 8px;
							height: 8px;
							@include bor-rad(50px);
							background: $blue;
							@include pos(abs, 14px null null 3px);
							z-index: 1;
							opacity: 0; }
						&.active {
							color: $black;
							&:after {
								opacity: 1; } } } }
				.filter-color {
					padding: 10px 10px 0;
					@include flexbox;
					flex-wrap: wrap;
					justify-content: flex-start;
					a {
						@include flexbox;
						justify-content: center;
						flex: 0 0 12.5%;
						.color-img {
							width: 24px;
							height: 24px;
							@include bor-rad(50px);
							border: 1px solid darken($white,5%);
							box-sizing: content-box;
							@include pos(rel,null);
							margin-bottom: 10px;
							&:before {
								content: '';
								@include pos(abs, 0px null 0px 0px);
								right: 0px;
								@include bor-rad(50px);
								border: 3px solid $white;
								box-sizing: border-box; } }
						&.active {
							.color-img {
								border-color: $blue;
								&:before {
 } } } } }									// border-color: $blue
				.filter-properties {}
				.filter-price {} } } }
	.filter-tags {
		@include media-breakpoint-down(md) {
			padding: 0 15px;
			margin-top: 10px; }
		> a {
			color: darken($white,40%);
			@include fs(13rem);
			margin-left: 10px;
			&:hover {
				color: $red; } }
		.filter-tag {
			display: inline-block;
			margin-bottom: 10px;
			@include bor-rad(50px);
			background: $blue;
			color: $white;
			@include fs(11rem);
			line-height: 1;
			cursor: pointer;
			padding: 3px 3px 3px 5px;
			.btn-removetag {
				display: inline-block;
				vertical-align: middle;
				@include bor-rad(50px);
				width: 14px;
				height: 14px;
				font-weight: bold;
				line-height: 15px;
				text-align: center;
				@include bor-rad(50px);
				color: $blue;
				background: $white;
				margin-left: 5px;
				a {
					color: $red;
					margin-left: 0; }
				.material-icons {
					@include fs(12rem);
					margin-top: 1px; } } } }
	.filter-button {
		padding: 12px 15px;
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: center;
		text-transform: uppercase;
		@include fs(13rem);
		@include pos(fix, null 0 0 0);
		z-index: 5;
		@include media-breakpoint-up(lg) {
			display: none; }
		.btn-resetall {
			display: block;
			width: 50%;
			color: $bluegrey;
			text-align: center;
			font-weight: 500;
			a {
				color: darken($white,40%);
				font-weight: 500; } }
		.btn-closefilter {
			width: 50%;
			font-weight: 500;
			color: $white;
			background: $main;
			@include bor-rad(50px);
			padding: 0px 30px;
			line-height: 36px;
			text-align: center;
			&:hover {
				background: $blue; } } } }
