// DROPDOWN
.dropdown {
	&.show {
		.dropdown-toggle {
 } } }			// color: $blue
.dropdown-toggle {
	&:after {
		display: none;
		border: none;
		width: auto;
		height: auto; } }
.dropdown-menu {
	border: none;
	@include bor-rad(0px);
	background: $white;
	border-top: 2px solid $blue;
	margin-top: 10px;
	padding: 0;
	box-shadow: 5px 10px 20px rgba(0, 0, 0, 0.15);
	.dropdown-title {
		@include fs(14rem);
		text-transform: uppercase;
		font-weight: bold;
		padding: 15px 15px;
		border-bottom: 1px solid darken($white,10%); }
	.dropdown-item {
		&:hover, &.active {
			background: color-rgba($blue,0.05);
			color: $blue;
			border: none; } } }
// DROPDOWN CUSTOM
.dropdown-custom {
	> a {
		display: block;
		@include fs(14rem);
		font-weight: 600;
		line-height: 1.2;
		&:hover {
			color: $blue; }
		+ a {
			@include fs(12rem);
			font-weight: normal;
			color: darken($white,40%);
			&:hover {
				color: lighten($red,10%); } } }
	.dropdown-menu {
		@include media-breakpoint-down(sm) {
			border: none;
			@include bor-rad(0);
			margin: 0;
			position: fixed !important;
			top: 0 !important;
			right: 0 !important;
			bottom: 0 !important;
			left: 0 !important;
			@include transform(none !important);
			z-index: 1000; }
		.dropdown-title {
			@include media-breakpoint-down(sm) {
				@include fs(18rem);
				text-transform: capitalize;
				font-weight: 500; }
			.btn-closedropdown {
				@include pos(abs, 12px 15px null null);
				cursor: pointer;
				@include media-breakpoint-down(sm) {
					@include pos(static,null);
					height: 50px;
					@include flexbox;
					align-items: center;
					align-content: center;
					margin-right: 15px; }
				&:before {
					content: 'close';
					@extend .material-icons;
					@include flexbox;
					@include fs(16rem);
					width: 26px;
					height: 26px;
					@include bor-rad(50px);
					background: $blue;
					color: $white;
					align-items: center;
					align-content: center;
					justify-content: center;
					@include media-breakpoint-down(sm) {
						content: 'arrow_back';
						@include fs(24rem);
						width: unset;
						height: unset;
						display: block;
						background: none;
						color: $main; } }
				&:hover {
					color: $red;
					@include media-breakpoint-down(sm) {
						color: $main; } } } } } }
