header.header1 {
	background: $color-primary;
	.header-wrap {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: space-between; }
	.logo {
		height: 64px;
		@include flexbox;
		align-items: center;
		align-content: center;
		a {
			display: block;
			img {
				height: 42px; } } }
	ul.menu-link {
		@include flexbox;
		align-items: center;
		align-content: center;
		@include fs(15rem);
		li {
			+ li {
				margin-left: 25px; }
			a {
				color: $white; } } }
	.header-right {
		@include flexbox;
		align-items: center;
		align-content: center;
		.search {
			.search-toggle {
				color: $white;
				@include fs(24rem);
				line-height: 1; } }
		.language {
			margin-left: 20px;
			.language-toggle {
				color: $white;
				@include fs(24rem);
				line-height: 1; } } } }

// header2
header.header2 {
	background: linear-gradient(to right, $color-default, $color-primary);
	.header-wrap {
		@include flexbox;
		align-items: center;
		align-content: center;
		justify-content: space-between; }
	.logo {
		height: 60px;
		@include flexbox;
		align-items: center;
		align-content: center;
		a {
			display: block;
			img {
				height: 36px; } } }
	ul.menu-link {
		@include flexbox;
		align-items: center;
		align-content: center;
		@include fs(14rem);
		font-weight: 300;
		li {
			border-bottom: 1px solid transparent;
			+ li {
				margin-left: 35px; }
			a {
				color: $white;
				text-transform: uppercase; }
			&:hover {
				border-bottom: 1px solid $white; } } }
	.header-right {
		@include flexbox;
		align-items: center;
		align-content: center;
		.search {
			.search-toggle {
				color: $white;
				@include fs(18rem);
				line-height: 1; } }
		.language {
			margin-left: 35px;
			.language-toggle {
				color: $white;
				@include fs(18rem);
				line-height: 1; } } } }
