.page-title {
	@include fs(28rem);
	font-weight: 500; }

.hot-news {
	background: $white;
	margin-top: 50px;
	margin-bottom: 30px;
	@include transition(all,0.3s);
	&:hover {
		box-shadow: 10px 10px 20px 0px rgba(0,0,0,0.04), -10px 0 20px 0px rgba(0,0,0,0.04);
		figure {
			.news-img {
				img {
					@include transform(scale3d(1.1,1.1,1)); } } } }
	figure {
		@include flexbox;
		.news-img {
			width: 66.66%;
			overflow: hidden;
			img {
				width: 100%;
				height: 380px;
				object-fit: cover;
				@include transition(all,0.3s); } }
		figcaption {
			width: 33.33%;
			padding: 60px 40px;
			@include flexbox;
			flex-direction: column;
			justify-content: center;
			.news-cate {
				color: $color-default;
				text-transform: uppercase;
				@include fs(13rem);
				font-weight: 500;
				margin-bottom: 10px; }
			.news-name {
				color: $main;
				@include fs(24rem);
				line-height: 1.2;
				font-weight: 500;
				margin-bottom: 15px;
				@include textover(3); }
			.news-des {
				@include fs(16rem);
				color: darken($white,60%);
				@include textover(3);
				margin-bottom: 30px; }
			time {
				@include fs(12rem);
				color: darken($white,40%);
				letter-spacing: 1px; } } } }

.news-list {
	.news-item {
		background: $white;
		margin-bottom: 30px;
		@include transition(all,0.3s);
		&:hover {
			box-shadow: 10px 10px 20px 0px rgba(0,0,0,0.04), -10px 0 20px 0px rgba(0,0,0,0.04);
			figure {
				.news-img {
					img {
						@include transform(scale3d(1.1,1.1,1)); } } } }
		figure {
			.news-img {
				overflow: hidden;
				img {
					width: 100%;
					height: 220px;
					object-fit: cover;
					@include transition(all,0.3s); } }
			figcaption {
				padding: 30px;
				.news-cate {
					color: $color-default;
					text-transform: uppercase;
					@include fs(12rem);
					// font-weight: 500
					margin-bottom: 5px; }
				.news-name {
					color: $main;
					@include fs(18rem);
					line-height: 1.2;
					font-weight: 500;
					margin-bottom: 10px;
					min-height: 42px;
					@include textover(2); }
				time {
					display: block;
					@include fs(12rem);
					color: darken($white,40%);
					letter-spacing: 1px;
					margin-bottom: 10px; }
				.news-des {
					@include fs(14rem);
					color: darken($white,60%);
					@include textover(2); } } } }

	.col-lg-6 {
		.news-item {
			figure {
				.news-img {
					img {
						height: 300px; } } } } } }

.news-page {
	.btn-wrap {
		text-align: center;
		margin-bottom: 30px; }
	.btn-viewmore {
		text-transform: uppercase;
		&:hover {
			color: $color-default;
			&:after {
					margin-left: 10px; } }
		&:after {
			content: '\e876';
			font-family: Linearicons-Free;
			margin-left: 3px; } } }

.hot-news2 {
	margin: 20px -100px 40px;
	.news-item {
		@include pos(rel,null);
		@include bor-rad(10px);
		overflow: hidden;
		&:before {
			content: '';
			@include pos(abs, 0 null 0 0);
			right: 0;
			z-index: 1;
			background: linear-gradient(color-rgba($black,0),color-rgba($black,0.5)); }
		&.big {
			figure {
				figcaption {
					padding: 40px;
					.news-name {
						@include fs(28rem);
						margin: 10px 0 15px; } } }
			&:hover {
				figure {
					figcaption {
						padding-bottom: 70px;
						.btn-link {
							opacity: 1;
							margin-bottom: 0; } } } } }
		figure {
			.news-img {
				img {
					width: 100%;
					height: 430px;
					object-fit: cover; } }
			figcaption {
				@include pos(abs, null 0 0 0);
				z-index: 2;
				padding: 20px;
				@include transition(all,0.3s);
				.news-cate {
					display: inline-block;
					vertical-align: middle;
					background: linear-gradient(to right, $color-default, $color-primary);
					color: $white;
					@include fs(11rem);
					line-height: 1;
					letter-spacing: 1px;
					padding: 3px 10px;
					text-transform: uppercase;
					font-weight: 500; }
				time {
					display: inline-block;
					vertical-align: middle;
					color: $white;
					@include fs(11rem);
					&:before {
						content: '\e864';
						font-family: Linearicons-Free;
						margin-right: 3px; } }
				.news-name {
					@include fs(18rem);
					line-height: 1.3;
					@include textover(2);
					font-weight: 700;
					color: $white;
					margin: 10px 0 0; }
				.news-des {
					@include fs(13rem);
					@include textover(2);
					color: $white; }
				.btn-link {
					@include fs(12rem);
					font-weight: 500;
					text-transform: uppercase;
					color: $white;
					@include pos(abs, null 40px 40px null);
					@include transition(all,0.3s);
					margin-bottom: -20px;
					opacity: 0;
					&:hover {
						text-decoration: none; }
					&:after {
						content: '\e87a';
						font-family: Linearicons-Free;
						margin-left: 5px;
						@include fs(10rem); } } } } } }

.news-page2 {}
.news-group {
	margin-bottom: 40px; }
.news-cate-wrap {
	@include flexbox;
	justify-content: space-between;
	align-items: center;
	align-content: center;
	margin-bottom: 40px;
	.news-cate-name {
		background: $main;
		color: $white;
		@include fs(14rem);
		font-weight: 500;
		text-transform: uppercase;
		padding: 3px 20px; }
	.btn-viewmore {
		@include fs(13rem);
		color: darken($white,60%);
		&:after {
			content: "\e87a";
			font-family: Linearicons-Free;
			@include fs(11rem);
			margin-left: 5px; }
		&:hover {
			color: $color-default; } } }
