	.news-item {
		margin-bottom: 40px;
		figure {
			@include flexbox;
			align-items: center;
			align-content: center;
			.news-img {
				width: 250px;
				@include bor-rad(5px);
				overflow: hidden;
				img {
					width: 100%;
					height: 180px;
					object-fit: cover; } }
			figcaption {
				flex: 1;
				padding-left: 20px;
				.news-name {
					@include fs(20rem);
					line-height: 1.3;
					font-weight: 500;
					@include textover(2);
					margin-bottom: 10px; }
				.news-des {
					@include fs(14rem);
					@include textover(2);
					color: darken($white,60%);
					margin-bottom: 20px; }
				.link-wrap {
					text-align: right; }
				.btn-link {
					@include fs(11rem);
					font-weight: 700;
					text-transform: uppercase;
					color: darken($white,60%);
					&:after {
						content: "\e87a";
						font-family: Linearicons-Free;
						@include fs(11rem);
						font-weight: normal;
						margin-left: 5px; } } } } } }

.sidebar-title {
	@include fs(14rem);
	font-weight: 500;
	text-transform: uppercase;
	margin: 3px 0px 15px; }
.sidebar-categories {
	margin-bottom: 40px;
	.categories-link {
		li {
			margin-bottom: 10px;
			a {
				display: block;
				@include fs(14rem);
				font-weight: 500;
				@include bor-rad(10px);
				background: darken($white,10%);
				padding: 10px 20px;
				&:hover {
					background: linear-gradient(to right, $color-default, $color-primary);
					color: $white; } } } } }

.sidebar-news {
	margin-bottom: 40px; }
.news-list3 {
