			content: '';
			display: block;
			width: 10px;
			height: 10px;
			@include bor-rad(3px);
			background: $color-default;
			@include pos(abs, 6px null null 0); } }
	a {
		@include fs(15rem);
		font-weight: 500;
		&:hover {
			color: $color-default; } } }

