.br-theme-fontawesome-stars {
	.br-widget {
		height: auto;
		@include flexbox;
		align-items: center;
		align-content: center;
		a {
			display: block;
			@include fs(14rem);
			&:after {}
			&.br-active, &.br-selected {
				&:after {
					color: $amber; } } } } }
.br-theme-fontawesome-stars .br-widget a.br-selected:after {}
